import React from "react";
import classes from "../../assets/styles/Skill.module.scss";

const SkillCard = ({ name, icon, link, theme, key }) => {
  return (
    <div
      className={[
        classes.Card,
        `${!theme ? classes.LightCard : classes.DarkCard}`,
      ].join(" ")}
      key={key}
    >
      <a
        href={link}
        target="_blank"
        rel="noopener noreferrer"
        style={{ textDecoration: "none" }}
      >
        {icon}
        <p>{name}</p>
      </a>
    </div>
  );
};

export default SkillCard;
